  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden; }
  .embed-responsive::before {
    content: '';
    display: block;
    padding-top: 56.25%; }
  .embed-responsive iframe,
  .embed-responsive video {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0; }
  .embed-responsive--4-3::before {
    padding-top: 75%; }

.user {
  margin-top: 35px;
  margin-bottom: 60px;
  padding: 0 30px; }
  .user__avatar {
    width: 105px;
    height: 105px;
    border: 4px solid #d0d0d0;
    border-radius: 50%;
    margin: 0 auto 20px auto;
    background-size: cover; }
    .user__avatar--sm {
      width: 60px;
      height: 60px;
      border: none; }
  .user__name {
    font-size: 18px;
    font-weight: 700;
    text-align: center; }

.dev {
  border-top: 1px solid #d0d0d0;
  color: #d0d0d0;
  text-align: center;
  padding: 30px 15px;
  margin: 0 10px; }
  .dev a {
    color: #2b62a6;
    text-decoration: none; }

.mentor {
  padding: 8px 0;
  margin-bottom: 40px;
  display: flex; }
  .mentor__avatar {
    width: 100px;
    height: 100px;
    background-color: gray;
    border-radius: 50%;
    background-size: cover;
    flex-shrink: 0;
    margin-right: 30px; }

.task__header {
  font-weight: 700;
  overflow: hidden;
  margin-bottom: 20px; }

.task__name {
  font-size: 18px; }

.task__video {
  margin-bottom: 30px; }

.task__link-group {
  display: flex;
  margin-bottom: 30px; }

.task__link + .task__link {
  margin-left: 20px; }

.task__link {
  color: #000;
  display: flex;
  align-items: center; }
  .task__link .icon {
    flex-shrink: 0;
    margin-right: 10px; }

.task__result {
  font-size: 18px;
  margin-bottom: 30px; }

.content-card {
  background: #fff;
  border: 1px solid #d0d0d0;
  border-radius: 5px;
  padding: 30px;
  max-width: 520px;
  margin-bottom: 30px; }
  .content-card__title {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 18px; }
  .content-card--lg {
    max-width: 615px; }
  .content-card--xl {
    max-width: 850px; }

.field-link {
  margin-bottom: 20px;
  margin-top: 20px; }
  .field-link__item {
    display: flex;
    align-items: center;
    color: #000; }
    .field-link__item .icon {
      margin-right: 10px; }

.tags {
  margin-bottom: 30px; }
  .tags__added, .tags__all {
    display: flex;
    flex-wrap: wrap; }
  .tags__added {
    margin-bottom: 20px; }
  .tags__all .tags__item {
    cursor: pointer; }
  .tags__item {
    background: #fff;
    padding: 5px 15px;
    border: 1px solid #d0d0d0;
    border-radius: 20px;
    font-size: 12px;
    position: relative;
    margin-right: 10px; }
    .tags__item .close {
      position: absolute;
      top: -10px;
      right: -12px;
      background: #e65c5c;
      width: 24px;
      height: 24px;
      padding: 5px;
      border-radius: 50%;
      cursor: pointer; }
      .tags__item .close span:before, .tags__item .close span:after {
        background: #fff;
        height: 2px; }

.courses__wrap {
  background: #fff;
  padding: 30px;
  border-radius: 5px; }

.courses__header {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between; }
  .courses__header .search {
    margin-top: 20px; }

.courses__nav {
  display: flex; }

.courses__nav-item {
  color: #d0d0d0;
  text-decoration: none;
  padding: 20px;
  border-bottom: 2px solid #d0d0d0; }
  .courses__nav-item:hover, .courses__nav-item:focus {
    color: #000; }
  .courses__nav-item--active {
    color: #2b62a6;
    border-color: #2b62a6; }
    .courses__nav-item--active:hover, .courses__nav-item--active:focus {
      color: #2b62a6; }

.courses__counter {
  margin-bottom: 20px;
  flex: 0 0 100%;
  max-width: 100%;
  padding-left: 15px;
  padding-right: 15px; }

.courses__row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px; }
  .courses__row > * {
    flex-basis: 100%; }

.courses__card {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 5px;
  margin-bottom: 30px; }

.courses__card-link {
  text-decoration: none; }

.courses__card-image {
  width: 100%;
  padding-bottom: 40%;
  background-size: cover; }

.courses__card-name {
  border: 1px solid #d0d0d0;
  padding: 20px;
  line-height: 1.4;
  font-size: 18px; }

.comments__item {
  display: flex;
  margin-top: 1.375em;
  margin-bottom: 1.375em; }

.comments__item-picture {
  flex-shrink: 0;
  margin-right: 1em; }

.comments__item-body {
  flex-grow: 1; }
  .comments__item-body *:first-child {
    margin-top: 0; }

.comments__name-user {
  font-weight: 700; }

.comments__item-date {
  flex-shrink: 0;
  margin-left: 1em; }

.board {
  max-width: 1145px; }
  .board__header {
    background: #fff;
    padding: 30px;
    display: flex;
    justify-content: space-between; }
  .board__body .table-mobile-scroll,
  .board__body .table {
    margin: 0; }
  .board__nav {
    display: flex;
    font-size: 16px; }
  .board__nav-item {
    color: #d0d0d0;
    font-weight: 500;
    text-decoration: none;
    padding: 8px 0; }
    .board__nav-item--active {
      color: #000; }
  .board__separate {
    width: 2px;
    background: #8f8f8f;
    margin: 0 30px; }
  .board__table-header {
    background: #2b62a6;
    color: #fff;
    padding: 10px; }
  .board__table-row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px; }
    .board__table-row > * {
      flex-basis: 100%; }
  .board__table-col {
    flex: 0 0 25%;
    max-width: 25%;
    padding-left: 15px;
    padding-right: 15px; }

.search .field-text__input {
  border-radius: 5px 0 0 5px; }
  .search .field-text__input:focus {
    box-shadow: none; }

.search .btn {
  padding: 5px 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 5px 5px 0; }

@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important; }
  a,
  a:visited {
    text-decoration: underline !important; }
  a[href]:after {
    content: " (" attr(href) ")"; }
  a[href^='#']:after,
  a[href^='javascript:']:after {
    content: ''; }
  abbr[title]:after {
    content: " (" attr(title) ")"; }
  pre,
  blockquote {
    border: 1px solid #999 !important;
    page-break-inside: avoid; }
  thead {
    display: table-header-group; }
  tr,
  img {
    page-break-inside: avoid; }
  img {
    max-width: 100% !important; }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3; }
  h2,
  h3 {
    page-break-after: avoid; }
  table {
    border-collapse: collapse !important; }
    table td,
    table th {
      background-color: #fff !important; } }
