// В этом файле должны быть стили для БЭМ-блока courses, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.courses {

  $block-name:                &; // #{$block-name}__element

  &__wrap {
    background: #fff;
    padding: 30px;
    border-radius: 5px;
  }

  &__header {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    .search {
      margin-top: 20px;
    }
  }

  &__nav {
    display: flex;
  }

  &__nav-item {
    color: $gray-light;
    text-decoration: none;
    padding: 20px;
    border-bottom: 2px solid $gray-light;

    &:hover,
    &:focus {
      color: #000;
    }

    &--active {
      color: $color-main;
      border-color: $color-main;
      &:hover,
      &:focus {
        color: $color-main;
      }
    }
  }

  &__counter {
    margin-bottom: 20px;
    @include make-col(12);
  }

  &__row {
    @include row;
  }

  &__card {
    @include make-col(4);
    border-radius: 5px;
    margin-bottom: 30px;
  }

  &__card-link {
    text-decoration: none;
  }

  &__card-image {
    width: 100%;
    padding-bottom: 40%;
    background-size: cover;
  }
  &__card-name {
    border: 1px solid $gray-light;
    padding: 20px;
    line-height: 1.4;
    font-size: 18px;
  }

}
